<template>
  <v-row justify="center">
    <v-dialog v-model="show" scrollable max-width="400px">
      <v-card>
        <v-card-title>{{ title }}</v-card-title>
        <v-card-text style="height: 600px;">
          <v-radio-group v-model="current" column>
            <v-radio v-for="(item, i) in items" :label="item" :key="i" :value="item" ></v-radio>
          </v-radio-group>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn color="blue darken-1" text @click="show = false">Close</v-btn>
          <v-btn color="blue darken-1" text @click="select(current)">Select</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  export default {
    name: 'Dictionary',
    props: {
      value: Boolean,
      title: String,
      select: Function,
      items: Array,
      current: String,
    },
    computed: {
      show: {
        get () {
          return this.value
        },
        set (value) {
          this.$emit('input', value)
        }
      }
    }
  }
</script>