<template>
  <v-row justify="center">
    <v-dialog v-model="show" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="show = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Device configuration</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark text >Retrieve</v-btn>
            <v-btn dark text @click="show = false">Close</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-tabs
          v-model="tab"
          background-color="white"
          color="primary"
        >
          <v-tabs-slider color="primary"></v-tabs-slider>
          <v-tab v-for="item in tabs" :key="item">
            {{ item }}
          </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item
            key="information"
          >
            <v-card flat>
              <v-card-text >
                <device-item-config-info :item="item">
                </device-item-config-info>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  import DeviceItemConfigInfo from './Devices.config.info.vue'
  export default {
    name: 'DeviceItemConfig',
    data: function(){
      return {
        tab: null,
        tabs: [
          'information', 'modules', 'interfaces',
        ],
      }
    },
    props: {
      value: Boolean,
      item: Object,
    },
    computed: {
      show: {
        get () {
          return this.value
        },
        set (value) {
          this.$emit('input', value)
        }
      }
    },
    components: {
      DeviceItemConfigInfo,
    }
  }
</script>