<template>
  <v-row justify="center">
    <v-dialog v-model="show" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="show = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Output settings</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark text @click="applySettings(headers.headers_selected)">Apply</v-btn>
            <v-btn dark text @click="saveSettings(headers.headers_selected)">Save</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-container>
          <v-row>
            <v-col>
              <v-card
                class="mx-auto"
                max-width="300"
                tile
              >
                <v-subheader>
                  Selected fields (showed)
                </v-subheader>
                <v-list>
                  <draggable v-model="headers.headers_selected" :options="{group:'devices'}" style="min-height: 10px">
                    <template v-for="item in headers.headers_selected">
                      <v-card :key="item.text" class="ma-4" outlined elevation="4">
                        <v-card-title>
                          {{ item.text }}
                        </v-card-title>
                      </v-card>
                    </template>
                  </draggable>
                </v-list>
              </v-card>
            </v-col>
            <v-col cols="2">
              Drag-n-drop blocks with field names to customize table view
            </v-col>
            <v-col>
              <v-card
                class="mx-auto"
                max-width="300"
                tile
              >
                <v-subheader>
                  Available fields (hidden)
                </v-subheader>
                <v-list>
                  <draggable v-model="headers.headers_allowed" :options="{group:'devices'}" style="min-height: 10px">
                    <template v-for="item in headers.headers_allowed">
                      <v-card :key="item.text" class="ma-4" outlined elevation="4">
                        <v-card-title>
                          {{ item.text }}
                        </v-card-title>
                      </v-card>
                    </template>
                  </draggable>
                </v-list>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-dialog>
    <template>
      <dialog-alert 
        v-model="alert_dialog"
        :message="alert_message"
      >
      </dialog-alert>
    </template>
  </v-row>
</template>

<script>
  import draggable from "vuedraggable";
  export default {
    name: 'DevicesSettings',
    data: function(){
      return {
        alert_dialog: false,
        alert_message: '',
      }
    },
    props: {
      value: Boolean,
      headers: Array,
      applySettings: Function,
    },
    methods: {
      saveSettings(headers_selected) {
        let settings = {
          'devices_output_settings': {
            'fields': headers_selected
          }
        }
        this.$http.patch('backend/user_settings', settings)
        .then((response) => {
          if (response['status'] == 200) {
            if (response.data['status'] == 'success') {
              this.applySettings(headers_selected)
            } else if (response.data['status'] == 'error') {
              this.alert_dialog_open(response.data['error'])
            }
          }
          this.item_dialog = false
        })
        .catch((error) => {
          console.log(error);
          this.alert_dialog_open('something is wrong!')
        })
      },
      alert_dialog_open(alert_message) {
        this.alert_dialog = true
        this.alert_message = alert_message
      },
    },
    computed: {
      show: {
        get () {
          return this.value
        },
        set (value) {
          this.$emit('input', value)
        }
      }
    },
    components: {
      draggable,
    }
  }
</script>