<template>
  <div>
    <v-card>
      <v-subheader>
        Devices
        <v-spacer></v-spacer>
        <v-btn icon class="mr-5">
          <v-btn
            light
            fab
            fixed
            small
            @click="showSettings"
          >
            <v-icon>mdi-tune</v-icon>
          </v-btn>
        </v-btn>
        <v-btn icon v-if="canWrite">
          <v-btn
            color="pink"
            dark
            fab
            fixed
            small
            @click="addItem"
          >
            <v-icon>mdi-plus</v-icon>
          </v-btn>
        </v-btn>
      </v-subheader>
      <v-card-title>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Filter"
          single-line
          hide-details
          dense
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="devices"
        :search="search"
        sort-by="name"
      >
        <template v-slot:item.actions="{ item }">
          <v-icon
            v-if="canWrite"
            small
            class="mr-2"
            @click="editItem(item)"
            title="Edit item"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            v-if="canWrite"
            small
            class="mr-2"
            @click="deleteItem(item)"
            title="Delete item"
          >
            mdi-delete
          </v-icon>
          <v-icon
            small
            @click="showItemConfig(item)"
            title="Show item info"
          >
            mdi-eye
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
    <template>
      <device-item 
        v-model="item_dialog"
        :item="item"
        :title="item._id=='' ? 'New device' : 'Edit device'"
        :save="saveItem"
        :devices="devices"
      >
      </device-item>
    </template>
    <template>
      <device-item-config
        v-model="item_config_dialog"
        :item="item"
      >
      </device-item-config>
    </template>
    <template>
      <devices-settings
        v-model="settings_dialog"
        :headers="headers_all"
        :applySettings="applySettings"
      >
      </devices-settings>
    </template>
    <template>
      <dialog-confirm 
        v-model="confirm_dialog"
        title="Are you sure?"
        :message="confirm_message"
        :confirm="confirmDeleteItem"
      >
      </dialog-confirm>
    </template>
    <template>
      <dialog-alert 
        v-model="alert_dialog"
        :message="alert_message"
      >
      </dialog-alert>
    </template>
  </div>
</template>

<script>
  import DeviceItem from '../components/Devices.item.vue'
  import DeviceItemConfig from '../components/Devices.config.vue'
  import DialogConfirm from '../components/Dialog.confirm.vue'
  import DialogAlert from '../components/Dialog.alert.vue'
  import DevicesSettings from '../components/Devices.settings.vue'

  export default {
    data () {
      return {
        search: '',
        headers_all: {
          headers_selected: [],
          headers_allowed: [],
        },
        headers: [],
        all_fields: [
          { text: 'Name', value: 'name'},
          { text: 'IP address', value: 'management.address' },
          { text: 'Vendor', value: 'vendor' },
          { text: 'Model', value: 'model'},
          { text: 'Location', value: 'location'},
          { text: 'Type', value: 'type'}
        ],
        default_fields: [
          { text: 'Name', value: 'name'},
          { text: 'IP address', value: 'management.address' },
          { text: 'Vendor', value: 'vendor' },
          { text: 'Model', value: 'model'},
        ],
        devices: [],
        item: {
          management:{},
        },
        empty_item: {
          _id: '',
          name: '',
          type: '',
          vendor: '',
          model: '',
          location: '',
          management: {
            address: '',
            snmp_read_communty: '',
            cli_protocol: 'ssh',
            cli_username: '',
            cli_password: '',
            cli_enable_password: '',
          }
        },
        deletedIndex: -1,
        item_dialog: false,
        item_config_dialog: false,
        alert_dialog: false,
        alert_message: '',
        confirm_dialog: false,
        confirm_message: '',
        settings_dialog: false,
      }
    },
    methods: {
      getSettings () {
        this.$http.get('backend/user_settings')
        .then((response) => {
          if (response['status'] == 200) {
            if (response.data['status']== 'success'){
              try {
                this.applySettings(response['data']['data']['settings']['device_output']['fields'])
              }
              catch {
                this.applySettings(this.default_fields)
              }
            }
          }
        })
        .catch((error) => {
          console.log(error);
        })
      },
      getList () {
        this.$http.get('backend/devices')
        .then((response) => {
          if (response['status'] == 200) {
            this.devices = response['data']['devices'] || {}
          }
        })
        .catch((error) => {
          console.log(error);
        })
      },
      addItem () {
        // clear item object
        this.item = JSON.parse(JSON.stringify(this.empty_item));
        this.item_dialog = true
      },
      showItemConfig (item) {
        this.$http.get('backend/device/' + item['_id'])
        .then((response) => {
          if (response['status'] == 200) {
            if (response.data['status']== 'success'){
              this.item = Object.assign(this.item, this.empty_item, response.data['data'])
              this.item_config_dialog = true
            } else {
              this.alert_dialog_open(response.data['error'])
            }
          }
        })
        .catch((error) => {
          console.log(error);
        })
      },
      editItem (item) {
        this.$http.get('backend/device/' + item['_id'])
        .then((response) => {
          if (response['status'] == 200) {
            if (response.data['status']== 'success'){
              this.item = Object.assign(this.item, this.empty_item, response.data['data'])
              this.item_dialog = true
            } else {
              this.alert_dialog_open(response.data['error'])
            }
          }
        })
        .catch((error) => {
          console.log(error);
        })
      },
      deleteItem (item) {
        this.deletedIndex = this.devices.indexOf(item)
        this.confirm_message = 'Delete this device?<br/> You will not be able to recover it'
        this.confirm_dialog = true
      },
      saveItem() {
        if (this.checkInputData()) {
          if (this.item._id === '') {
            // new device
            this.$http.post('backend/device/', this.item)
            .then((response) => {
              if (response['status'] == 200) {
                if (response.data['status'] == 'success') {
                  this.item = Object.assign(this.item, {_id: response['data']['_id']})
                  this.devices.push(this.item)
                  this.item_dialog = false
                } else if (response.data['status'] == 'error') {
                  this.alert_dialog_open(response.data['error'])
                }
              }
            })
            .catch((error) => {
              console.log(error);
              this.alert_dialog_open('something is wrong!')
            })
          } else {
            // existed device
            this.$http.put('backend/device/'+ this.item._id, this.item)
            .then((response) => {
              if (response['status'] == 200) {
                if (response.data['status'] == 'success') {
                  this.devices.every( (item) => {
                    if (item._id == this.item._id) {
                      item = Object.assign(item, this.item)
                      return false
                    }
                    return true
                  })
                } else if (response.data['status'] == 'error') {
                  this.alert_dialog_open(response.data['error'])
                }
              }
              this.item_dialog = false
            })
            .catch((error) => {
              console.log(error);
              this.alert_dialog_open('something is wrong!')
            })
          }
        }
      },
      checkInputData () {
        if (this.item.name == '') {
          this.alert_dialog_open('Name is required')
          return false
        }
        const check_duplicates = this.devices.every( (item) => {
          if (item._id != this.item._id) {
            if (item.name && item.name == this.item.name) {
              this.alert_dialog_open('Duplicated name!')
              return false
            }
            if (item.management && item.management.address === this.item.management.address && this.item.management.address != '') {
              this.alert_dialog_open('Duplicated management address!')
              return false
            }
          }
          return true
        })
        return check_duplicates
      },
      alert_dialog_open(alert_message) {
        this.alert_dialog = true
        this.alert_message = alert_message
      },
      confirmDeleteItem() {
        this.$http.delete('backend/device/' + this.devices[this.deletedIndex]['_id'])
        .then((response) => {
          if (response['status'] == 200) {
            if (response.data['status'] == 'success') {
              this.devices.splice(this.deletedIndex, 1)
            }
          }
        })
        .catch((error) => {
          console.log(error);
        })
        this.confirm_dialog = false
      },
      showSettings () {
        this.headers_all.headers_selected = this.headers.filter(
          header => header.value != 'actions'
        )
        this.headers_all.headers_allowed = this.all_fields.filter(
          header => {
            return !this.headers_all.headers_selected.some(header1 => {
              return header.value == header1.value
            })
          }
        )
        this.settings_dialog = true
      },
      applySettings (headers){
        this.headers = headers.slice()
        this.headers.push({
          text: 'Actions', value: 'actions', filterable: false, sortable: false
        })
        this.settings_dialog = false
      }
    },
    computed: {
      canWrite () {
        if (this.$store.getters.permissions['superuser']) {
          return true
        } else {
          return this.$store.getters.permissions['devices'] &&
                 this.$store.getters.permissions['devices'] == 'write'
        }
      }
    },
    created: function() {
      this.getList();
      this.getSettings()
    },
    components: {
      DeviceItem,
      DeviceItemConfig,
      DialogConfirm,
      DialogAlert,
      DevicesSettings,
    }
  }
</script>
