<template>
  <v-row justify="center">
    <v-dialog v-model="show" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="show = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>{{ title}}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark text @click="save">Save</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-container>
          <v-row>
            <v-col cols="6">
              <v-card>
                <v-card-subtitle>Common</v-card-subtitle>
                <v-container>
                  <v-row>
                    <v-col cols="8" sm="12" md="8">
                      <v-text-field 
                        label="Name*"
                        outlined
                        dense
                        clearable
                        autofocus
                        v-model="item.name"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col cols="4" sm="12" md="4">
                      <v-select
                        :items="type"
                        label="Type*"
                        dense
                        outlined
                        v-model="item.type"
                      ></v-select>
                    </v-col>
                    <v-col cols="6" sm="12" md="6">
                      <v-text-field
                        label="Vendor" 
                        outlined
                        dense
                        append-icon="mdi-notebook"
                        @click:append="showSelectVendor"
                        v-model="item.vendor"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="6" sm="12" md="6">
                      <v-text-field
                        label="Model" 
                        outlined
                        dense
                        append-icon="mdi-notebook"
                        @click:append="showSelectModel"
                        v-model="item.model"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field 
                        label="Location"
                        outlined
                        dense
                        append-icon="mdi-notebook"
                        @click:append="showSelectLocation"
                        v-model="item.location"
                      >
                      </v-text-field>
                    </v-col>
                  </v-row>
                  <small>*indicates required field</small>
                </v-container>
              </v-card>
            </v-col>
            <v-col cols="6">
              <v-card>
                <v-card-subtitle>Management</v-card-subtitle>
                <v-container>
                  <v-row>
                    <v-col cols="6" sm="12" md="6">
                      <v-text-field 
                        label="Management address"
                        outlined
                        dense
                        clearable
                        v-model="item.management.address"
                        hint="IP or FQDN"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col cols="6" sm="12" md="6">
                      <v-text-field 
                        label="SNMPv2 read community"
                        outlined
                        dense
                        clearable
                        type="text"
                        v-model="item.management.snmp_read_community"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col cols="6" sm="12" md="6">
                      <v-select
                        :items="proto"
                        label="Protocol"
                        dense
                        outlined
                        v-model="item.management.protocol"
                      ></v-select>
                    </v-col>
                    <v-col cols="6" sm="12" md="6">
                      <v-text-field 
                        label="CLI username"
                        outlined
                        dense
                        clearable
                        v-model="item.management.cli_username"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col cols="6" sm="12" md="6">
                      <v-text-field 
                        label="CLI password"
                        outlined
                        dense
                        clearable
                        type="password"
                        v-model="item.management.cli_password"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col cols="6" sm="12" md="6">
                      <v-text-field 
                        label="CLI enable password"
                        outlined
                        dense
                        clearable
                        type="password"
                        v-model="item.management.cli_enable_password"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-select
                        :items="profile"
                        label="Profile"
                        dense
                        outlined
                        v-model="item.management.profile"
                      ></v-select>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-dialog>
    <template>
      <dictionary
        v-model="select_vendor_dialog"
        title="Select vendor"
        :select="selectVendor"
        :items="vendors"
        :current="item.vendor"
      >
      </dictionary>
    </template>
    <template>
      <dictionary
        v-model="select_model_dialog"
        title="Select model"
        :select="selectModel"
        :items="models"
        :current="item.model"
      >
      </dictionary>
    </template>
    <template>
      <dictionary
        v-model="select_location_dialog"
        title="Select location"
        :items="locations"
        :select="selectLocation"
        :current="item.location"
      >
      </dictionary>
    </template>
  </v-row>
</template>

<script>
  import Dictionary from './Dictionary.vue'
  export default {
    name: 'DeviceItem',
    data: function(){
      return {
        proto: ['telnet','ssh'],
        type: ['router','switch', 'security'],
        profile: ['CiscoIOS'],
        vendors: [],
        models: [],
        locations: [],
        select_vendor_dialog: false,
        select_model_dialog: false,
        select_location_dialog: false,
      }
    },
    props: {
      value: Boolean,
      item: Object,
      title: String,
      save: Function,
      devices: Array,
    },
    methods: {
      showSelectVendor () {
        this.vendors = this.get_vendor_names()
        this.select_vendor_dialog = true
      },
      showSelectModel () {
        this.models = this.get_model_names()
        this.select_model_dialog = true
      },
      showSelectLocation () {
        this.locations = this.get_location_names()
        this.select_location_dialog = true
      },
      selectVendor (selected_item) {
        this.item.vendor = selected_item
        this.select_vendor_dialog = false
      },
      selectModel (selected_item) {
        this.item.model = selected_item
        this.select_model_dialog = false
      },
      selectLocation (selected_item) {
        this.item.location = selected_item
        this.select_location_dialog = false
      },
      get_vendor_names() {
        return Array.from(new Set(
          Array.from(this.devices, ({vendor}) => vendor)
        )).filter(item => item != '').sort()  
      },
      get_model_names() {
        if (this.item.vendor == '') {
          return Array.from(new Set(
            Array.from(this.devices, ({model}) => model)
          )).filter(item => item != '').sort()
        } else {
          return Array.from(new Set(
            Array.from(this.devices.filter(
              item => item.vendor == this.item.vendor
            ), ({model}) => model)
          )).filter(item => item != '').sort()
        }
      },
      get_location_names() {
        return Array.from(new Set(
          Array.from(this.devices, ({location}) => location)
        )).filter(item => item != '').sort()  
      },
    },
    computed: {
      show: {
        get () {
          return this.value
        },
        set (value) {
          this.$emit('input', value)
        }
      }
    },
    components: {
      Dictionary,
    }
  }
</script>